"use client";

import dynamic from "next/dynamic";
import React from "react";

// üß† Dynamic safe imports ‚Äî page won't go blank if one fails
const NavBar = dynamic(() => import("../components/NavBar").catch(() => () => (
  <div className="text-red-500 text-center p-6">‚ö†Ô∏è NavBar failed to load</div>
)), { ssr: false });

const HeroSection = dynamic(() => import("./HeroSection").catch(() => () => (
  <div className="text-red-500 text-center p-6">‚ö†Ô∏è HeroSection failed</div>
)), { ssr: false });

const FabricSection = dynamic(() => import("./FabricSection").catch(() => () => (
  <div className="text-red-500 text-center p-6">‚ö†Ô∏è FabricSection failed</div>
)), { ssr: false });

const MuscleMapping = dynamic(() => import("./MuscleMapping").catch(() => () => (
  <div className="text-red-500 text-center p-6">‚ö†Ô∏è MuscleMapping failed</div>
)), { ssr: false });

const RearView = dynamic(() => import("./RearView").catch(() => () => (
  <div className="text-red-500 text-center p-6">‚ö†Ô∏è RearView failed</div>
)), { ssr: false });

const Endorsements = dynamic(() => import("./Endorsements").catch(() => () => (
  <div className="text-red-500 text-center p-6">‚ö†Ô∏è Endorsements failed</div>
)), { ssr: false });

export default function LuxeSculptPage() {
  return (
    <main className="flex flex-col items-center justify-center min-h-screen bg-black text-white fade-in">
      {/* üåü NAVBAR */}
      <NavBar />

      {/* üèÜ HERO SECTION */}
      <section id="hero" className="w-full">
        <HeroSection />
      </section>

      {/* üßµ FABRIC SECTION */}
      <section id="fabric" className="w-full">
        <FabricSection />
      </section>

      {/* üí™ MUSCLE MAPPING SECTION */}
      <section id="muscle-mapping" className="w-full">
        <MuscleMapping />
      </section>

      {/* üîô REAR VIEW SECTION */}
      <section id="rear-view" className="w-full">
        <RearView />
      </section>

      {/* ‚≠ê ENDORSEMENTS SECTION */}
      <section id="endorsements" className="w-full">
        <Endorsements />
      </section>
    </main>
  );
}
